body {
    font-family: 'Montserrat', sans-serif;
    color: #111;
    background-color: #fafafa;
  }
  * {
    margin: 0;
    padding: 0;
  }
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  /*============================ MOBILE=============================== */
  /*Reviews*/
  
  @media screen and (min-width: 375px) and (max-width: 767px) {
    .container {
      padding: 0 20px;
      margin: 0 auto;
    }
    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
    .reviews-card-nazar,
    .reviews-image-olena {
      display: none;
    }
    .reviews-image-nazar,
    .reviews-box-tablet {
      display: none;
    }
    .reviews-card-olena {
      display: none;
    }
    .reviews-header {
      color: #fd9222;
      text-align: center;
      font-size: 20px;
      font-style: normal;
      font-weight: 600;
      line-height: 24px; /* 120% */
      letter-spacing: -0.6px;
      text-transform: uppercase;
      padding-bottom: 50px;
      margin-top: 72px;
    }
    .reviews-accent {
      color: #111;
    }
    .reviews-banner {
      display: flex;
      flex-direction: row;
      align-self: center;
      list-style: none;
      align-items: center;
      justify-content: center;
      gap: 13px;
      padding-top: 20px;
    }
    .reviews-button {
      display: flex;
      padding: 12px 28px;
      align-items: flex-start;
      gap: 14px;
      border-radius: 100px;
      border: 1px solid #fd9222;
      min-width: 109px;
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      margin: 32px auto 72px;
      background-color: transparent;
    }
    .reviews-container {
      display: flex;
      width: 335px;
      height: 404px;
      flex-direction: column;
      gap: 32px;
      flex-shrink: 0;
    }
    .reviews-item {
      display: flex;
      min-width: 344px;
      height: 242px;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
    }
    .reviews-image {
      border-radius: 80px;
      background-color: rgba(211, 211, 211, 0.5);
      background-repeat: no-repeat;
      position: absolute;
      z-index: 1;
    }
    .reviews-card {
      border-radius: 15px;
      background-position: 50%;
      border: 1px solid rgba(17, 17, 17, 0.1);
      background: #fff;
      box-shadow: 0px 9.04932px 26.57166px 0px rgba(215, 228, 249, 0.25);
      height: 200px;
      width: 335px;
      position: relative;
      margin-top: 42px;
    }
    .reviews-name {
      text-align: center;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 24px; /* 133.333% */
      padding-top: 56px;
    }
    .reviews-paragraph {
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px; /* 128.571% */
      letter-spacing: -0.28px;
      width: 299px;
      height: 54px;
      padding-top: 24px;
      margin: auto;
    }
    .reviews-box {
      display: flex;
      width: 107px;
      height: 242px;
      flex-shrink: 0;
      justify-content: center;
    }
  }
