* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

@media screen and (max-width: 767px) {
  .section_advantages {
    background: var(--dark);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 758px;
    margin: 0 auto;
    padding: 20px;
    display: flex;
  }

  .visually-hidden-advantages {
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }

  .list_advantages {
    flex-direction: column;
    gap: 20px;
    width: 335px;
    list-style-type: none;
    display: flex;
  }

  .advantages_points {
    max-width: 480px;
    padding-right: 28px;
  }

