/* <<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<< MOBILE >>>>>>>>>>>>>>>>>>>>>>>>>*/
@media screen and (min-width: 375px) and (max-width:767px) {

    /*<<<<<<<<<<<<<<<<<<<<<<<<<<<<<hero>>>>>>>>>>>>>>>>>>>>>>>*/
    .hero {
        display: flex;
        padding: 14px 20px 71px;
        width: 100%;
        height: 706px;
        justify-content: center;
    }

    .hero-container {
        color: #ffffff;
        width: 335px;
        height: 621px;
        background-image: url(/src/images/hero/chocolate-covered-cocoa-powder-p-2x.jpg);
        border-radius: 30px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        background-size: cover;
        background-position: center;
    }

    .hero-list {
        list-style: none;
        text-decoration: none;
        display: flex;
        flex-wrap: wrap;
        gap: 32px 12px;
        padding: 0;
        margin: 0 20px 21px;
    }

    .hero-heading {
        display: none;
    }

    .hero-acapit {
        color: #ffffff;
        font-family: Montserrat;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: -0.6px;
        margin: 0;
    }

    .hero-acapit-box {
        width: 295px;
        height: 72px;
        flex-shrink: 0;
    }

    .hero-butons {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        gap: 32px 14px;
        margin: 0;
        padding: 0;
        justify-content: flex-start;
    }

    .hero-button-buy {
        width: 120px;
        height: 41px;
        padding: 12px 28px;
        border-radius: 100px;
        color: #ffffff;
        background-color: #fd9222;
        border: none;
        font-size: 14px;
        font-family: Montserrat;
        font-weight: 600;
        line-height: normal;
        font-style: normal;
    }
    .hero-button-buy:hover{
        background-color: #FCF1E0;
        color: #fd9222;
    }

    .hero-button-how {
        width: 159px;
        height: 41px;
        border: 1px solid #ffffff;
        border-radius: 100px;
        background-color: transparent;
        color: #ffffff;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        text-decoration: none;
        list-style: none;
    }
    .hero-how{
        color: #ffffff;
        text-decoration: none;
    }
    .hero-button-how:hover {
        border: 1px solid var(--orange);
        color: var(--orange);
    }
    
    .hero-button-how:hover>.hero-how {
        color: var(--orange);
    }

    .hero-scroll-li {
        display: flex;
        gap: 12px;
        align-items: center;
    }
    .hero-scroll-link {
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 14px;
        fill: var(--orange);
    }
    
    .hero-scroll-link:hover>.hero-scroll {
        color: var(--orange);
    }
    
    .hero-scroll-link:hover>.hero-button-scroll {
        background-color: var(--orange);
    }
    
    .hero-scroll-link:hover>.hero-button-scroll>.hero-icon-scroll {
        fill: var(--white);
    }

    .hero-scroll {
        color: #ffffff;
        font-family: Montserrat;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 14px;
        letter-spacing: -0.36px;
    }

    .hero-button-scroll {
        background-color: var(--white);
        border: none;
        border-radius: 50%;
        width: 28px;
        height: 28px;
    }

    .hero-icon-scroll {
        background-color: transparent;
        width: 16px;
        height: 27px;
        align-items: center;
        justify-content: center;
    }
}

